.container {
    max-width: 90%;
    width: 800px;
    margin: 50px auto;
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
    text-align: center;
  
    @media (max-width: 600px) {
      width: 95%;
      padding: 15px;
    }
  }
  
  h2, h3 {
    color: #333;
    margin-top: 20px;
  
    @media (max-width: 600px) {
      font-size: 22px;
    }
  }
  
  p {
    font-size: 18px;
    color: #666;
    text-align: justify;
  
    @media (max-width: 600px) {
      font-size: 16px;
    }
  }
  
  .table-img-phys {
    max-width: 90%;
    height: auto;
    margin: 15px 0;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  
    @media (max-width: 600px) {
      max-width: 95%;
    }
  }
  