.container {
  max-width: 90%;
  width: 800px;
  margin: 50px auto;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  text-align: center;

  @media (max-width: 600px) {
    width: 95%;
    padding: 15px;
  }
}

h2, h3 {
  color: #333;
  margin-top: 20px;

  @media (max-width: 600px) {
    font-size: 22px;
  }
}

p {
  font-size: 18px;
  color: #666;
  text-align: justify;

  @media (max-width: 600px) {
    font-size: 16px;
  }
}

.table-img-inform {
  max-width: 90%;
  height: auto;
  margin: 15px 0;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);

  @media (max-width: 600px) {
    max-width: 70%;
  }
}

.links-grid {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin: 20px auto;
  align-items: center;

  @media (max-width: 600px) {
    gap: 12px;
  }
}

.info-link {
  display: block;
  width: 100%;
  max-width: 600px;
  background-color: #f0f8ff;
  padding: 15px 20px;
  text-align: center;
  border-radius: 10px;
  font-size: 18px;
  color: #0073e6;
  font-weight: bold;
  text-decoration: none;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease-in-out;

  &:hover {
    background-color: #e6f2ff;
    transform: translateY(-2px);
  }

  @media (max-width: 600px) {
    font-size: 16px;
    padding: 12px 16px;
    max-width: 95%;
  }
}
