.python-card-switcher {
    background: #1e1e1e;
    color: #f8f8f2;
    border-radius: 16px;
    box-shadow: 0 6px 18px rgba(0, 0, 0, 0.15);
    padding: 20px;
    max-width: 800px;
    margin: 20px auto;

    .switcher-title {
        text-align: center;
        font-size: 22px;
        margin-bottom: 16px;
        color: #f8f8f2;
      }
  
    .switcher-buttons {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      margin-bottom: 16px;
      gap: 10px;
  
      button {
        background: #333;
        color: #fff;
        border: none;
        padding: 8px 16px;
        border-radius: 8px;
        cursor: pointer;
        transition: background 0.3s;
  
        &.active {
          background: #0073e6;
        }
  
        &:hover {
          background: #555;
        }
        @media (max-width: 600px) {
            &.active {
                background: #0073e6;
              }
        }
      }
      
    }
  
    pre {
      border-radius: 12px !important;
      font-size: 16px;
      overflow-x: auto;
    }
  
    @media (max-width: 600px) {
      padding: 16px;
  
      pre {
        font-size: 14px;
      }
  
      .switcher-buttons {
        flex-direction: column;
        align-items: stretch;
      }
      
      
    }
    .code-header {
        display: flex;
        justify-content: flex-end;
        margin-bottom: 8px;
      
        .copy-button {
          background-color: #0073e6;
          color: white;
          border: none;
          padding: 6px 12px;
          font-size: 14px;
          border-radius: 6px;
          cursor: pointer;
          transition: background 0.2s;
      
          &:hover {
            background-color: #005bb5;
          }
        }
      }
      
  }
  